import React, { useEffect } from 'react';
import { Canvas } from './Canvas';

export interface NodeType {
    val: string | number;
}

export const Node: React.FC<NodeType> = (props) => {
    const { val } = props;
    var canvas = document?.querySelectorAll('myCanvas')?.getContext('2d');

    useEffect(() => {
        console.log(canvas);
        const ctx: any = document?.getElementById('myCanvas')?.getContext('2d');
        ctx.beginPath();
        ctx.arc(75, 75, 50, 0, Math.PI * 2, true);
        ctx.lineWidth = 2;
        ctx.stroke();
        ctx.font = '30px Arial';
        ctx.fillText(val, 75 - 10, 75 + 10);
    }, []);

    return (
        <>
            <Canvas></Canvas>
        </>
    );
};
